import { REMOVE_ALERT, SET_ALERT } from './types'
import { v4 } from 'uuid'

export const setAlert = (msg, alertType, timeout=5000) => dispatch => {
   const id = v4()
   dispatch({
      type: SET_ALERT,
      payload: { id, msg, alertType }
   })
   setTimeout(() => {
      dispatch({
         type: REMOVE_ALERT,
         payload: id
      })
   }, timeout)
}